import React, { useState } from "react";

import * as card from "./styled/card";

const CARD_OFFSET = 10;
const SCALE_FACTOR = 0.06; //  

interface StackedCardsProps {
  src: string; //    prop
  images: { [key: string]: { src: string; alt: string } }; //    
}

export const StackedCards: React.FC<StackedCardsProps> = (props) => {
  const { src, images } = props;
  const imageKeys = Object.keys(images); //      
  const [cards, setCards] = useState(imageKeys); //  
  const [swipedRight, setSwipedRight] = useState(false);

  //       
  const moveToEnd = (from: number) => {
    setCards((prevCards) => {
      const updatedCards = [...prevCards];
      const movedCard = updatedCards.splice(from, 1);
      updatedCards.push(movedCard[0]);
      return updatedCards;
    });
  };

  //   
  const handleClick = () => {
    setSwipedRight(true);
    setTimeout(() => {
      moveToEnd(0);
      setSwipedRight(false);
    }, 300); // 0.3     
  };

  return (
    <card.wrapperStyle>
      <card.CardWrap>
        {cards.map((imageName, index) => (
          <card.CardItem
            key={imageName}
            style={{
              cursor: index === 0 ? "grab" : "auto",
              zIndex: imageKeys.length - index,
            }}
            animate={{
              top: index * -CARD_OFFSET,
              scale: 1 - index * SCALE_FACTOR,
              rotate: index === 0 ? (swipedRight ? -45 : 0) : 0, //    
            }}
            drag={index === 0 ? "x" : false}
            dragConstraints={{ left: 0, right: 0 }}
            whileDrag={{ scale: 0.95 }}
            onTouchEnd={handleClick}
            onClick={handleClick}
          >
            <card.Image
              src={src + images[imageName].src} //     
              alt={src + images[imageName].alt} //      
            />
          </card.CardItem>
        ))}
      </card.CardWrap>
    </card.wrapperStyle>
  );
};
